.quizCategoryCard_linksContainer__1ZGaz{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1em; 
}
  .quizCategoryCard_quizLink__2NUkC{
    height: 100px;
    position: relative;
    padding: 20px 50px;
    text-decoration: none;
    color: #fff;
    font-size: 1em;
    text-transform: uppercase;
    font-family: sans-serif;
    letter-spacing: 4px;
    overflow: hidden;
    background: rgba(255,255,255,.1);
    box-shadow: 0 5px 5px rgba(0,0,0.2);
  }
  .quizCategoryCard_quizLink__2NUkC:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(255,255,255,.1);
  }
  .quizCategoryCard_quizLink__2NUkC:after{
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg,transparent,rgba(255,255,255,.4),transparent);
    transition: 0.5s;
    transition-delay: 0.5s;
  }
  .quizCategoryCard_quizLink__2NUkC:hover:after{
    left: 100%;
  }
  .quizCategoryCard_quizLink__2NUkC span{
    position: absolute;
    display: block;
    transition: 0.5s ease;
  }
  .quizCategoryCard_quizLink__2NUkC span:nth-child(1)
  {
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
    background: #fff;
  }
  .quizCategoryCard_quizLink__2NUkC:hover span:nth-child(1)
  {
    width: 100%;
    transform: translateX(100%);
  }
  .quizCategoryCard_quizLink__2NUkC span:nth-child(3)
  {
    bottom: 0;
    right: 0;
    width: 0;
    height: 1px;
    background: #fff;
  }
  .quizCategoryCard_quizLink__2NUkC:hover span:nth-child(3)
  {
    width: 100%;
    transform: translateX(-100%);
  }
  .quizCategoryCard_quizLink__2NUkC span:nth-child(2)
  {
    top: 0;
    left: 0;
    width: 1px;
    height: 0;
    background: #fff;
  }
  .quizCategoryCard_quizLink__2NUkC:hover span:nth-child(2)
  {
    height: 100%;
    transform: translateY(100%);
  }
  .quizCategoryCard_quizLink__2NUkC span:nth-child(4)
  {
    bottom: 0;
    right: 0;
    width: 1px;
    height: 0;
    background: #fff;
  }
  .quizCategoryCard_quizLink__2NUkC:hover span:nth-child(4)
  {
    height: 100%;
    transform: translateY(-100%);
  }
